@use "../global" as *;
.testimonial-slider-2 {
	&__prev {
		margin: 0;
		width: auto;
		height: auto;
		transform: translateY(-50%);
		@include screen(md) {
			transform: translate(-100%, -50%);
		}
		&::after {
			display: none;
		}
	}
	&__next {
		margin: 0;
		width: auto;
		height: auto;
		transform: translateY(-50%);
		@include screen(md) {
			transform: translate(100%, -50%);
		}
		&::after {
			display: none;
		}
	}
}
