@use "../global" as *;
.footer-2 {
	position: relative;
	isolation: isolate;
	background-image: url(../img/hero-2-bg-shape.png), linear-gradient(188deg, #23272c -32.56%, #1c1f23 62.55%);
	background-size: cover;
	background-position: center bottom;
	background-repeat: no-repeat;
	&::after {
		content: "";
		position: absolute;
		bottom: 15%;
		right: 5%;
		width: rem(200);
		height: rem(200);
		border-radius: 50%;
		background: linear-gradient(133deg, #e8428c 24.11%, #704ee7 75.89%);
		filter: blur(160px);
		z-index: -1;
	}
	&__shape-1 {
		position: absolute;
		left: 3%;
		bottom: 15%;
		z-index: -1;
	}
	&__shape-2 {
		position: absolute;
		right: 5%;
		bottom: 15%;
		z-index: -1;
	}
}
