@use "../global" as *;
.hero-3 {
	padding-top: rem(52);
	background-image: url(../img/hero-3-shape-bg.png);
	background-size: contain;
	background-position: center bottom;
	background-repeat: no-repeat;
	@include screen(lg) {
		padding-top: rem(72);
	}
	&::after {
		@include screen(lg) {
			content: "";
			position: absolute;
			left: 50%;
			top: 20%;
			transform: translateX(-50%);
			width: rem(250);
			height: rem(250);
			border-radius: 50%;
			background: linear-gradient(133deg, #e8428c 24.11%, #704ee7 75.89%);
			filter: blur(160px);
			opacity: 0.75;
			z-index: -1;
		}
	}
	&__img {
		&::after {
			@include screen(lg) {
				content: "";
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translateY(-50%);
				width: rem(250);
				height: rem(250);
				border-radius: 50%;
				background: var(--bs-primary);
				filter: blur(160px);
				z-index: -1;
			}
		}
	}
	&__shape {
		display: none;
		@include screen(lg) {
			display: inline-block;
			opacity: 0.2;
			position: absolute;
			z-index: -1;
			&-1 {
				top: 10%;
				right: 80%;
			}
			&-2 {
				top: 15%;
				left: 90%;
			}
			&-3 {
				bottom: 40%;
				left: 70%;
			}
			&-4 {
				bottom: 30%;
				right: 75%;
			}
		}
	}
}
