@use "../global" as *;
.info-list {
	@include screen(md) {
		& > li {
			width: 50%;
			&:nth-child(1) {
				border-right: 1px solid rgba(var(--bs-white-rgb), 0.15);
				border-bottom: 1px solid rgba(var(--bs-white-rgb), 0.15);
			}
			&:nth-child(2) {
				border-bottom: 1px solid rgba(var(--bs-white-rgb), 0.15);
			}
			&:nth-child(3) {
				border-right: 1px solid rgba(var(--bs-white-rgb), 0.15);
			}
		}
	}
}
