@use "../global" as *;
.navbar-overlay {
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 999;
	&::after {
		content: "";
		position: absolute;
		inset: 0;
		z-index: -1;
		background-color: rgba(255, 255, 255, 0.5);
		box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
		backdrop-filter: blur(5px);
		transition: all 0.3s ease;
		@include screen(lg) {
			background-color: transparent;
			box-shadow: none;
			backdrop-filter: blur(0);
		}
	}
	&.navbar--dark {
		&::after {
			background-color: var(--bs-dark);
			@include screen(lg) {
				background-color: transparent;
				box-shadow: none;
				backdrop-filter: blur(0);
			}
		}
		.nav-link {
			color: var(--bs-light);
		}
		.sub-menu {
			background-color: rgba(var(--bs-light-rgb), 0.1);
			@include screen(lg) {
				background-color: var(--bs-dark);
			}
		}
	}
}
.navbar-sticky {
	position: sticky;
	top: 0;
	z-index: 999;
	background-color: rgba(var(--bs-white-rgb), 1);
}
.navbar-nav {
	padding: 0.5rem 0.25rem;
}
.nav-item {
	position: relative;
	isolation: isolate;
	&.active {
		& > a {
			color: rgba(var(--bs-primary-rgb), 1);
		}
	}
}
.nav-link {
	&:hover {
		color: rgba(var(--bs-primary-rgb), 1);
	}
}
.navbar-toggler {
	display: grid;
	place-content: center;
	width: rem(36);
	height: rem(36);
	padding: 0;
	border-radius: 0.25rem;
	&:focus {
		outline: none;
		box-shadow: none;
	}
	&-icon {
		width: 1.5rem;
		height: 1.5rem;
	}
}
.navbar-sticky-init {
	.navbar-overlay {
		&::after {
			background-color: rgba(255, 255, 255, 0.75);
			box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
			backdrop-filter: blur(5px);
		}
		&.navbar--dark {
			&::after {
				background-color: var(--bs-dark);
			}
		}
	}
}
