@use "../global" as *;
.video-section {
	height: rem(500);
	background-image: url(../img/video-section-bg.png);
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	&::after {
		content: "";
		position: absolute;
		inset: 0;
		background: linear-gradient(270deg, rgba(35, 39, 44, 0.9) 0%, rgba(28, 31, 35, 0.15) 93.64%);
	}
}
