@use "../global" as *;
.work-process-card {
	display: grid;
	place-content: center;
	width: 100%;
	aspect-ratio: 1;
	border-radius: 50%;
	background-image: linear-gradient(120deg, #e8428c 24.11%, #704ee7 75.89%);
	position: relative;
	isolation: isolate;
	&:hover {
		.work-process-card__icon {
			background-image: linear-gradient(120deg, #e8428c 24.11%, #704ee7 75.89%);
		}
	}
	&::before {
		content: "";
		position: absolute;
		top: -1px;
		left: 50%;
		transform: translateX(-50%);
		width: rem(300);
		height: rem(300);
		border-radius: 50%;
		background-color: var(--bs-dark);
		z-index: -1;
	}
	&::after {
		content: "";
		position: absolute;
		inset: 1px;
		aspect-ratio: 1;
		background-color: var(--bs-dark);
		border-radius: 50%;
		z-index: -1;
	}
	&__icon {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translate(-50%, -50%);
		background-color: rgba(217, 217, 217, 0.2);
	}
}
