@use "../global" as *;
.popular-service-section {
	position: relative;
	isolation: isolate;
	overflow: hidden;
	&::before {
		content: "";
		position: absolute;
		top: 50%;
		left: 0;
		width: 100px;
		height: 300px;
		transform: rotate(-60deg), translate(-50%, -50%);
		border-radius: 100%;
		background: linear-gradient(88deg, rgba(232, 66, 140, 0.12) 0%, rgba(112, 78, 231, 0.12) 100%);
		filter: blur(60px);
		z-index: -1;
	}
	&::after {
		content: "";
		position: absolute;
		top: 50%;
		right: 0;
		width: 100px;
		height: 300px;
		transform: rotate(60deg), translate(-50%, -50%);
		border-radius: 100%;
		background: linear-gradient(88deg, rgba(232, 66, 140, 0.12) 0%, rgba(112, 78, 231, 0.12) 100%);
		filter: blur(60px);
		z-index: -1;
	}
}
