@use "../global" as *;
.about-us-section {
	position: relative;
	isolation: isolate;
	background-image: url(../img/hero-2-bg-shape.png);
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	&::after {
		content: "";
		position: absolute;
		bottom: 15%;
		right: 5%;
		width: rem(250);
		height: rem(250);
		border-radius: 50%;
		background: linear-gradient(133deg, #e8428c 24.11%, #704ee7 75.89%);
		filter: blur(160px);
		z-index: -1;
	}
}
