.service-slider {
	position: relative;
	&__nav {
		display: flex;
		align-items: center;
		justify-content: flex-end;
		gap: 1rem;
		&-prev {
			position: static;
			margin: 0;
			width: auto;
			height: auto;
			z-index: 0;
			&::after {
				display: none;
			}
		}
		&-next {
			position: static;
			margin: 0;
			width: auto;
			height: auto;
			z-index: 0;
			&::after {
				display: none;
			}
		}
	}
	&__slide {
		display: inline-block;
		position: relative;
		&::after {
			content: "";
			position: absolute;
			inset: 0;
			background: linear-gradient(180deg, rgba(53, 55, 57, 0) 11.59%, rgba(9, 9, 9, 0.49) 88.59%);
		}
	}
	&__overlay {
		&:hover {
			&::after {
				opacity: 1;
			}
		}
		&::after {
			content: "";
			position: absolute;
			inset: 0;
			background: linear-gradient(200deg, rgba(232, 66, 140, 0.17) 37.44%, rgba(112, 78, 231, 0.7) 76.21%);
			transition: all 0.3s ease;
			opacity: 0;
			z-index: -1;
		}
	}
	&__pagination {
		position: unset;
		.swiper-pagination-bullet-active {
			background-image: linear-gradient(92deg, var(--bs-danger) 13.13%, var(--bs-primary) 85.49%);
		}
	}
}
