@use "../global" as *;
.hero-2 {
	padding-top: rem(52);
	background-image: url(../img/hero-2-bg-shape.png);
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	@include screen(lg) {
		padding-top: rem(72);
	}
	&-img {
		&::after {
			content: "";
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			width: rem(250);
			height: rem(250);
			border-radius: 50%;
			background: linear-gradient(133deg, #e8428c 24.11%, #704ee7 75.89%);
			filter: blur(160px);
			z-index: -1;
		}
		&__1 {
			left: 0;
			bottom: 0;
			transform: translateX(-100%);
		}
		&__2 {
			right: 0;
			top: 0;
			transform: translateY(-100%);
		}
		&__3 {
			right: 0;
			bottom: 0;
			@media (min-width: 1920px) {
				transform: translateX(100%);
			}
		}
	}
}
