@use "../global" as *;
.ff-1 {
	font-family: var(--ff-1);
}

.h-2px {
	height: 2px;
}
.w-2px {
	width: 2px;
}

.cursor-pointer {
	cursor: pointer;
}

.pointer-none {
	pointer-events: none;
}

.form-control {
	&:focus {
		box-shadow: none;
		border-color: var(--bs-border-color);
	}
}

.form-select {
	&:focus {
		box-shadow: none;
		border-color: var(--bs-border-color);
	}
}

.\:focus-outline-0 {
	&:focus {
		outline: none;
	}
}

.link {
	text-decoration: none;
	transition: all 0.3s ease;
}

.inset-0 {
	inset: 0;
}

.transition {
	transition: all 0.3s ease;
}

.place-content-center {
	place-content: center;
}

.place-items-center {
	place-items: center;
}
.animate-pulse {
	&:after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		border-radius: 50%;
		background-image: linear-gradient(92deg, var(--bs-danger) 13.13%, var(--bs-primary) 85.49%);
		animation: pulse 2s infinite;
		pointer-events: none;
		z-index: -1;
	}
}
