.btn {
	--bs-btn-padding-x: 1.25rem;
	--bs-btn-padding-y: 0.5rem;
	--bs-btn-border-radius: 0.25rem;
	display: inline-flex;
	gap: 0.5rem;
	flex-shrink: 0;
	&.btn-icon {
		padding-inline: 0;
		align-items: center;
		justify-content: center;
		width: calc(2.5rem + 2px);
	}
}
.btn-xsm {
	--bs-btn-padding-x: 1rem;
	--bs-btn-padding-y: 0.25rem;
	--bs-btn-border-radius: 0.25rem;
	--bs-btn-font-size: 0.75rem;
	line-height: 1.334;
	&.btn-icon {
		padding-inline: 0;
		align-items: center;
		justify-content: center;
		width: calc(1.5rem + 2px);
	}
}
.btn-sm {
	--bs-btn-padding-x: 1rem;
	--bs-btn-padding-y: 0.25rem;
	--bs-btn-border-radius: 0.25rem;
	line-height: 1.715;
	&.btn-icon {
		padding-inline: 0;
		align-items: center;
		justify-content: center;
		width: calc(2rem + 2px);
	}
}
.btn-lg {
	--bs-btn-padding-x: 1.5rem;
	--bs-btn-padding-y: 0.5rem;
	--bs-btn-border-radius: 0.25rem;
	&.btn-icon {
		padding-inline: 0;
		align-items: center;
		justify-content: center;
		width: 3rem;
	}
}

.btn-danger {
	--bs-btn-color: #fff;
	--bs-btn-hover-color: #fff;
	--bs-btn-active-color: #fff;
	--bs-btn-disabled-color: #fff;
}
.btn-primary-gradient {
	background-image: linear-gradient(92deg, var(--bs-danger) 13.13%, var(--bs-primary) 85.49%);
}
.btn-primary-subtle {
	--bs-btn-font-weight: 400;
	--bs-btn-color: var(--bs-primary-text-emphasis);
	--bs-btn-bg: var(--bs-primary-bg-subtle);
	--bs-btn-border-color: var(--bs-primary-border-subtle);

	--bs-btn-hover-color: var(--bs-white);
	--bs-btn-hover-bg: rgba(var(--bs-primary-rgb), 1);
	--bs-btn-hover-border-color: rgba(var(--bs-primary-rgb), 1);

	--bs-btn-focus-shadow-rgb: var(--bs-primary-rgb);

	--bs-btn-active-color: var(--bs-white);
	--bs-btn-active-bg: rgba(var(--bs-primary-rgb), 1);
	--bs-btn-active-border-color: rgba(var(--bs-primary-rgb), 1);
}
