@use "../global" as *;
.logo {
	--logo-height: #{rem(36)};
	height: var(--logo-height);
	&__img {
		max-width: 100%;
		max-height: 100%;
		object-fit: contain;
		&-dark {
			display: none;
		}
	}
}
[data-bs-theme="dark"] {
	.logo__img-dark {
		display: inline-block;
	}
	.logo__img-light {
		display: none;
	}
}
