.pricing-card {
	position: relative;
	isolation: isolate;
	overflow: hidden;
	&:hover {
		&::after {
			opacity: 1;
		}
	}
	&.popular-pricing-card {
		&::after {
			opacity: 1;
		}
	}
	&::after {
		content: "";
		position: absolute;
		inset: 0;
		background-image: linear-gradient(120deg, #e8428c 24.11%, #704ee7 75.89%);
		border-radius: inherit;
		z-index: -2;
		opacity: 0.2;
		transition: all 0.3s ease;
	}
	&__head {
		position: relative;
		isolation: isolate;
		&::after {
			content: "";
			position: absolute;
			inset: 2px;
			background: var(--bs-dark);
			border-radius: 1rem 1rem 0 0;
			z-index: -1;
		}
	}
	&__footer {
		position: relative;
		isolation: isolate;
		&::after {
			content: "";
			position: absolute;
			inset: 2px;
			background: var(--bs-dark);
			border-radius: 0 0 1rem 1rem;
			z-index: -1;
		}
	}
}
