.process-card {
	background: linear-gradient(270deg, #23272c 1.87%, #1c1f23 100%);
	&:hover {
		.process-card__icon {
			background: -webkit-linear-gradient(45deg, var(--bs-danger), var(--bs-primary));
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
		.process-card__title {
			background: -webkit-linear-gradient(45deg, var(--bs-danger), var(--bs-primary));
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
		.process-card__btn {
			background-image: linear-gradient(92deg, var(--bs-danger) 13.13%, var(--bs-primary) 85.49%);
		}
	}
}
