.testimonial-nav {
	&__next {
		width: auto;
		height: auto;
		margin-top: 0;
		top: 50%;
		transform: translateY(-50%);
		right: 0;
		font-size: 2rem;
		line-height: 1;
		color: var(--bs-white);
		&::after {
			display: none;
		}
	}
	&__prev {
		width: auto;
		height: auto;
		margin-top: 0;
		top: 50%;
		transform: translateY(-50%);
		left: 0;
		font-size: 2rem;
		line-height: 1;
		color: var(--bs-white);
		&::after {
			display: none;
		}
	}
}
