.avatar {
	--avatar-size: 2rem;
	width: var(--avatar-size);
	height: var(--avatar-size);
	display: grid;
	place-content: center;
	border: 2px solid var(--bs-body-bg);
	border-radius: 50%;
	box-shadow: 0.25rem 0.25rem 0.5rem rgba(var(--bs-dark-rgb), 0.05);
	cursor: pointer;
	transition: all 0.3s ease;
	&-sm {
		--avatar-size: 1.5rem;
	}
	&-md {
		--avatar-size: 2.5rem;
	}
	&-lg {
		--avatar-size: 3rem;
	}
	&-xl {
		--avatar-size: 3.75rem;
	}
	&__img {
		width: 100%;
		aspect-ratio: 1;
		object-fit: cover;
		border-radius: inherit;
	}
	&-group {
		--avatar-size: 2rem;
		--avatar-column-size: calc(var(--avatar-size) / 2);
		--avatar-end-spacing: 1rem;
		display: grid;
		grid-template-columns: repeat(5, var(--avatar-column-size));
		width: 100%;
		padding-inline-end: var(--avatar-end-spacing);
		.avatar {
			--avatar-size: 2rem;
			&:hover {
				z-index: 1;
			}
		}
		&-sm {
			--avatar-size: 1.5rem;
			--avatar-end-spacing: 0.75rem;
			.avatar {
				--avatar-size: 1.5rem;
			}
		}
		&-md {
			--avatar-size: 2.5rem;
			--avatar-end-spacing: 1.25rem;
			.avatar {
				--avatar-size: 2.5rem;
			}
		}
		&-lg {
			--avatar-size: 3rem;
			--avatar-end-spacing: 1.5rem;
			.avatar {
				--avatar-size: 3rem;
			}
		}
		&-xl {
			--avatar-size: 3.75rem;
			--avatar-end-spacing: 1.75rem;
			.avatar {
				--avatar-size: 3.75rem;
			}
		}
	}
}
