:root {
	// Font Size (19px = 1vw @screen 1920px)
	--h1: clamp(2.4rem, 3.368vw + 1rem, 4rem); //64px
	--h2: clamp(1.8rem, 2.526vw + 1rem, 3rem); //48px
	--h3: clamp(1.5rem, 1.684vw + 1rem, 2rem); //32px
	--h4: clamp(1.3rem, 1.263vw + 1rem, 1.5rem); //24px
	--h5: clamp(1.2rem, 1.473vw + 1rem, 1.25rem); //20px
	--h6: clamp(1rem, 1.052vw + 1rem, 1.125rem); //18px
	--p: clamp(0.875rem, 0.842vw + 0.875rem, 1rem); //16px

	// Shadow Color
	--bs-box-shadow: 0 4px 8px -2px rgba(0, 0, 0, 0.15), 0 2px 4px -1px rgba(0, 0, 0, 0.1);
	--bs-box-shadow-sm: 0 2px 4px -1px rgba(0, 0, 0, 0.1);
	--bs-box-shadow-lg: 0 8px 16px -4px rgba(0, 0, 0, 0.15), 0 4px 8px -2px rgba(0, 0, 0, 0.1);

	// Border Color
	--bs-border-color: #e7e7e7;
	--bs-border-color-translucent: #e9e9e9;
}

[data-bs-theme="dark"] {
	// Shadow Color
	--bs-box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
	--bs-box-shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
	--bs-box-shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);

	// Border Color
	--bs-border-color: #495057;
	--bs-border-color-translucent: #495057;
}

$flex-directions: (row, column);
$grid-breakpoints: (
	xs: 0,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px,
	xxl: 1400px,
);
